.connect-section {
    display: flex;
    place-content: center;
    margin: 22em auto
}

.connect-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    gap: 2rem;
    flex-wrap: wrap;
    width: 50%;
}

.form-field-container {
    width: 100%;
}

.form-field-container form {
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.form-field-container input {
    display: inline-block;
    width: 100%;
    padding: 1rem 0px 4px 4px;
    border: 0;
    border-bottom: 1px solid black;
}

.connect-title {
    font-size: larger;
    font-weight: bold;
}

.connect-button {
    background-color: black;
    border-radius: 0.5em;
    color: white;
    padding: 0.5rem;
    text-align: center;
    font-family: 'SometypeMonoRegular';
    cursor: pointer;
    margin: 0.5em auto;
    border: 0;
    transition-duration: 0.15s;
}

.form-field-container .message-input {
    height: 7rem;
    border: 1px solid black;
    border-radius: .5em;
}

.connect-button:hover {
    background-color: rgb(0, 0, 0);
    color: rgb(123, 255, 123);
    transform: scale(1.05);
}

.connect-button:active {
    background-color: rgb(0, 0, 0);
    color: rgb(123, 255, 123);
    transform: scale(1.1);
}

@media screen and (max-width: 820px) {
    .connect-container {
        width: 80%;
    }
}