* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "SometypeMonoRegular", "Lucida Console", monaco, "Bitstream Vera Sans Mono", monospace;
  box-sizing: border-box;
}

img {
  display: block
}

h1 {
  font-size: 24px;
  font-style: normal;
  font-variant: normal;
  font-weight: 600;
  line-height: 26.4px;
  letter-spacing: 0.25rem;
}

h3 {
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  font-weight: 700;
  line-height: 15.4px;
}

p {
  font-size: 14px;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 20px;
}

blockquote {
  font-size: 21px;
  font-style: normal;
  font-variant: normal;
  font-weight: 400;
  line-height: 30px;
}

pre {
  font-size: 13px;
  font-style: normal;
  font-variant: normal;
  font-weight: 300;
  line-height: 18.5714px;
}